.card {
  position: relative;
  display: flex;
  flex-direction: column;

  width: 380px;
  height: 460px;
  background: linear-gradient(
    114.99deg,
    #471ca9 -0.99%,
    #5736a3 54.28%,
    #4b2a99 78.99%
  );
  box-shadow: -2.5777px 6.87386px 20.6216px rgba(0, 0, 0, 0.23);
  border-radius: 20px;
  font-size: 0px;
}

.logo {
  position: absolute;
  top: 20px;
  left: 20px;
}

.background {
  width: 308px;
  position: absolute;
  top: 28px;
  left: 50%;
  transform: translateX(-50%);
  opacity: 0.8;
  transition: opacity 500ms linear;
}

.card:hover .background {
  opacity: 1;
}

.line {
  position: absolute;
  width: 100%;
  height: 8px;
  left: 0px;
  top: 214px;

  background: #ebd8ff;
  box-shadow: 0px 3.43693px 3.43693px rgba(0, 0, 0, 0.06),
    inset 0px -1.71846px 3.43693px #ae7be3, inset 0px 3.43693px 2.5777px #fbf8ff;
}

.circle_thumb {
  position: absolute;
  left: 50%;
  top: 222px;

  transform: translate(-50%, -50%);
  width: 80px;
  height: 80px;
  box-shadow: inset 0px 13px 4px rgba(0, 0, 0, 0.06);
  border-radius: 50%;
}

.circle {
  border-radius: 50%;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.06);
  image-rendering: optimizeQuality;
}

.avatar_thumb {
  position: absolute;
  left: 50%;
  top: 222px;

  transform: translate(-50%, -50%);
  width: 80px;
  height: 80px;

  background-color: #4b2a99;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  overflow: hidden;
}

.stats {
  margin-top: 258px;

  font-size: 40px;
  font-family: "Montserrat";
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  text-transform: uppercase;
  color: #ebd8ff;
  text-align: center;
  padding: 26px;
}
.followersNumber {
  margin-right: 6px;
}
.name {
  font-size: 22px;
  position: absolute;
  top: 434px;
  left: 20px;
  max-width: 200px;
  opacity: 0;
  text-align: start;
  transform-origin: 0 0;
  transform: rotate(-90deg);
  transition: opacity 500ms linear;
}
.card:hover .name {
  opacity: 1;
}
.tweets {
  margin-bottom: 16px;
}
.button {
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 14px 56px;
  border-radius: 10.3108px;
  max-width: 196px;

  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  color: #373737;
  text-transform: uppercase;
  border-color: transparent;
  box-shadow: 0px 3.43693px 3.43693px rgba(0, 0, 0, 0.25);
  transform: scale(1);
  transition: transform 100ms linear;
}
.button:hover {
  cursor: pointer;
  transform: scale(1.02);
}
.followBtn {
  background-color: #ebd8ff;
}

.followingBtn {
  background-color: #5cd3a8;
}
